.container {
	background-color: black;
	max-width: 100%;
	height: 90px;
	padding: 10px;

	/* position: absolute; */
}

.container .main {
	display: flex;
	flex-direction: row;

	/* grid-gap: 1em; */
	max-width: 100%;
	/* margin: 0px 20px; */
	margin: 1% 8.7%;
	/* margin: auto; */
	align-items: center;
	text-align: center;
	justify-content: space-evenly;
}
.main div {
	padding: 0 11px;
}

.container .main .image img {
	max-width: 10vw;
	height: auto;
}
.but button {
	background-color: black; /* Green */
	/* border: none; */

	min-width: 8.5vw;
	height: 40px;
	color: white;
	padding: 5px 5px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 12px;
	border: 0.1px solid #00ceff;
	border-radius: 10px;
}
.bar {
	display: none;
	/* max-width: 10px; */
}
.bar img {
	max-width: 40px;
}
.but button:hover {
	border-color: #1383bb;
	transition: 0.5s;
}
.but button:active {
	border-color: #1383bb;
	transition: 0.5s;
}

/* cart popup */
#bag .popup-content {
	width: 400px !important;
	padding: 0px;
	height: 390px;
	overflow-y: scroll;
	overflow-x: hidden;
}
#bag ::-webkit-scrollbar {
	width: 10px;
	overflow: hidden;
}
#bag .popup-content .popup-arrow {
	position: fixed !important;
	left: 72% !important;
	top: 12% !important;
}
.but .cart__main {
	min-width: 400px;
	display: flex;
	flex-direction: column;
	padding: 0px;
	margin-bottom: 35px;
	/* background-color: red; */
}

.but .cart__main .cart__info {
	display: flex;
	flex-direction: row;
	padding: 10px 5px;
	width: 94%;
}
.but .cart__main .cart__info .cart__image img {
	width: 100px;
}
.cart__productname {
	text-align: left;
	width: 90%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 0px !important;
}
.but .cart__main .cart__detail {
	display: flex;
	flex-direction: column;
	padding: 0px;
	width: 75%;
	/* margin: 10px auto; */
}

.but .cart__main .cart__detail .cart__productinfo {
	display: flex;
	flex-direction: row;
	padding: 0px;
}
.but .cart__main .cart__detail .cart__productname h4 {
	font-size: 14px;
	font-weight: bold;
}
.but .cart__main .cart__detail .cart__productinfo h6 {
	font-size: 12px;
	/* font-weight: bold; */
}
.but .cart__main .cart__detail .cart__productinfo .cart__tenure,
.cart__rent,
.cart__deposit {
	display: flex;
	flex-direction: column;
	padding: 0px;
	margin: auto;
}

.but .cart__main .cart__button {
	display: flex;

	flex-direction: row;
	justify-content: space-between;
	padding: 0px;
	width: 91%;
}
.take_me_to_cart_but {
	position: fixed;
	margin-top: 320px;
	width: 380px;
}

.but .cart__main .cart__button .cart__edit,
.cart__checkout {
	width: 100%;
}
.but .cart__main .cart__button button {
	background-color: white; /* Green */
	/* border: none; */

	width: 100%;
	height: 30px;
	color: black;
	padding: 5px 5px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 10px;
	font-weight: 500;
	border: 0.1px solid #00ceff;
	border-radius: 10px;
}

.but .take_me_to_cart_but .cart_but {
	background-image: linear-gradient(to bottom right, #1bacf4, #19eeb5);
	width: 100%;
	height: 35px;
	color: white;
	padding: 10px;
	padding-bottom: 30px;
	text-align: center;
	text-decoration: none;
	display: block;
	font-size: 12px;
	font-weight: bold;
	border: 10px solid #fff;
	border-bottom: 13px solid #fff;
	border-radius: 14px;
}

.but .cart__main .cart__checkout button {
	background-image: linear-gradient(to bottom right, #1bacf4, #19eeb5);
	color: white;
}

