.searc {
	min-width: 30.5vw;
	height: 43px;
	border-radius: 9px;
	border-color: #00ceff;
	background-color: #ffffff;
	border-style: solid;
	border-width: thin;

	/* padding: 1vh 0vh; */
}
.searc input {
	border: none;
	padding: 8px 0px;
	float: left;
	min-width: 30vw;
	line-height: 0%;
}
.searc input:focus,
.but button:focus {
	outline: none;
}
.searc a i {
	background-color: #ffffff;
	margin: 12px 0px;
	float: rightS;
}

.but button img {
	max-height: 30px;
}
.but button :active {
	background-color: linear-gradient(to bottom right, #1bacf4, #19eeb5);
	transition-duration: 500;
	box-shadow: 0 0 10px -1px rgba(0, 0, 0, 0.2);
	-webkit-animation: cubic-bezier(0.075, 0.82, 0.165, 1);
	        animation: cubic-bezier(0.075, 0.82, 0.165, 1);
}

#sign button {
	background-image: linear-gradient(to bottom right, #1bacf4, #19eeb5);
	border-color: none;
	padding: 0.3vw;
}

#sign button :active {
	background-color: #19eeb5;
	transition-duration: 500;
	box-shadow: 0 0 10px -1px rgba(0, 0, 0, 0.2);

	-webkit-animation: cubic-bezier(0.075, 0.82, 0.165, 1);

	        animation: cubic-bezier(0.075, 0.82, 0.165, 1);
}
