@media screen and (max-width: 992px) {
	.container {
		max-width: 100%;
		height: 70px;
	}
	.searc,
	#bag,
	#sign {
		display: none;
	}
	#navmap {
		float: left;
	}
	.but button {
		border: 0.5px solid #00ceff;
	}
	.bar {
		display: inline;
		order: 3;
		float: right;
		font-size: 30px;
		/* margin: auto; */
		margin: 0px auto;
		margin-right: 0px;
	}
	.image {
		order: 2;
		margin: auto;
		max-width: 100px;
	}
	.container .main {
		margin: 0px 0px;
	}
	.container .main .image img {
		max-width: 25vw;
		height: auto;
		margin: auto;
	}
	.but button {
		border-radius: 5px;
		height: auto;
		padding: 0px 5px;
		margin: 0px 10px 0px 0px;
	}
	.notification__bell button {
		border-radius: 5px;
		height: auto;
		padding: 0px 5px;
		margin: 0px 10px 0px 0px;
	}

	.popup-content.pop-content {
		margin: 20px;
		left: -9.0885px !important;
	}
}

