.category_overview {
	display: flex;
	flex-direction: column;
	background-color: #ffffff;
}

.category_overview .staticContent {
	top: 90px;
}

.category_overview .scrollContent {
	margin-top: 0px;
}

.category_overview .pagePath {
	background-color: #fff;
	color: #000;
	font-weight: bolder;
	display: flex;
	flex-direction: row;
	max-width: 80%;
	margin: 0 auto;
	padding: 0px;
	padding-left: 40px;
	max-height: 28px;
}

.category_overview .pagePath p {
	font-size: 13px;
	font-weight: 500;
}

.category_overview .categoriesBackground {
	background-color: #f4f4f4;
	width: 100%;
}

.category_overview .categoriesBackground .categories {
	max-width: 80%;
	margin: 0 auto;
	display: flex;
	padding: 13px;
	padding-left: 40px;
	flex-direction: row;
}

.category_overview .categoriesBackground .categories .categoryButton {
	border: 0.4px solid;
	border-style: solid;
	background-image: none;
	border-radius: 5px;
	margin-right: 10px;
	border-color: #000;
	font-weight: bold;
	padding: 5px 5px;
	color: #000;
	width: 120px;
	font-size: 12px;
	height: 35px;
}

.category_overview .selectedButton {
	background-image: linear-gradient(to bottom right, #1bacf4, #19eeb5);
	border: 0.4px solid transparent;
	border-radius: 5px;
	margin-right: 10px;
	font-weight: bold;
	padding: 5px 5px;
	color: #ffffff;
	width: 120px;
	font-size: 12px;
	height: 35px;
}

.category_overview .subCategoriesBackground {
	width: 100%;
	background-color: #ffffff;
	box-shadow: 0 4px 8px -4px rgba(0, 0, 0, 0.4);
}

.category_overview .subCategoriesBackground .subCategories {
	display: flex;
	flex-direction: row;
	max-width: 80%;
	margin: 0 auto;
	padding: 0px 20px 0px 20px;
}
.category_overview .subCategoriesBackground .subCategories hr {
	border-bottom: 5px solid #1bacf4;
	width: 75%;
	margin: 0 auto;
	margin-top: 10px;
	margin-left: 15%;
	opacity: 0;
}

.category_overview .subCategoriesBackground .subCategories img {
	height: 60px;
	width: 60px;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
}

.category_overview .subCategoriesBackground .subCategories .subCard {
	margin: 20px 15px;
	margin-left: 20px;
	margin-bottom: 0px;
	box-shadow: 0 0 10px -1px rgba(0, 0, 0, 0.2);
	border: none;
	padding: 0px 20px 0px 20px;
	border-radius: 10px;
	height: auto;
}

.category_overview .packagesCard {
	margin-left: 12%;
}

.category_overview .subCategoriesBackground .subCategories .subCard:hover {
	text-decoration: none;
}

.category_overview .subCategoriesBackground .subCategories .subCard p {
	font-weight: 500;
	color: #000;
	padding-bottom: 10px;
	font-size: 12px;
}

.category_overview .product_overview button {
	float: right;
	border: none;
	padding: 3px 10px;
	font-size: 9px;
	background-color: #1bacf4;
	border-radius: 5px;
	font-weight: 700;
	color: #fff;
}
.category_overview .product_overview button :active {
	background-color: #19eeb5;
	transition-duration: 500;
	box-shadow: 0 0 10px -1px rgba(0, 0, 0, 0.2);

	animation: cubic-bezier(0.075, 0.82, 0.165, 1);
}

/* ----------------------------------------------- */
.category_overview .product_overview {
	max-width: 80%;
	display: flex;
	flex-direction: column;
	min-height: 500px;
	/* margin-left: 10vw; */
	margin: auto;
	/* overflow: hidden; */
}
.category_overview .product_overview .Heading h1 {
	font-size: 30px;
	font-weight: bold;
	float: left;
	padding: 30px 20px;
}
.category_overview .product_overview .category {
	display: flex;
	flex-direction: row;

	margin:20px 0px 0px 20px;
	border: none;
	flex-wrap: wrap;
	/* flex-flow: row; */
}
.category_overview .product_overview .category .catcard {
	display: flex;
	flex-direction: column;
	float: left;
	margin: 20px 19px;
	/* margin-left: 0px; */
	box-shadow: 0px 2px 7px 5px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	border: none;
	justify-content: space-evenly;
	text-decoration: none;
}

.category_overview .product_overview .category .ccard:hover {
	color: black;
	text-decoration: none;
}

.category_overview .product_overview .category .catcard img {
	/* height: 30vh; */
	max-width: 245px;
	border-radius: 10px;
}
.category_overview .product_overview .category .catcard .cathead {
	padding: 10px 20px 0px;
	text-align: left;
	font-weight: bold;

	/* margin: 200px; */
}
.category_overview .product_overview button {
	float: right;
	border: none;
	padding: 3px 10px;
	font-size: 8px;
	background-color: #1bacf4;
	border-radius: 5px;
	font-weight: bold;
	color: white;
	height: 22px;
}
.category_overview .product_overview button:active {
	background-color: #19eeb5;
	transition-duration: 500;
	box-shadow: 0 0 10px -1px rgba(0, 0, 0, 0.2);

	animation: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.category_overview .product_overview .header {
	font-family: 'Roboto', sans-serif;
	font-size: 12px;
}
.category_overview .product_overview .category .catcard .ccard {
	border-radius: 10px;
	border: 0px transparent;
	justify-content: space-around;
	min-height: 245px;
	max-width: 245px;
	color: black;
	text-decoration: none;
}
.category_overview .product_overview .date {
	font-size: 7px;
}

/* -------------------------------------- */
.category_overview .search_boy_with_input {
	max-width: 100%;
	display: flex;
	flex-direction: column;
	padding: 20px;
}
.category_overview .search_boy_with_input h1 {
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	font-size: 18px;
}
.category_overview .search_boy_with_input .search_boy img {
	max-width: 50%;
}
.category_overview .search_boy_with_input .search_boy_input {
	display: flex;
	flex-direction: row;
	justify-items: space-between;
	margin: auto;
	padding: 20px;
}
.category_overview .search_boy_with_input .search_boy_input input {
	min-width: 20vw;
	margin: auto;
	padding: 7px 5px;
	border: 0.4px solid;
	border-radius: 10px;
	font-family: 'Montserrat', sans-serif;
	margin-right: 20px;
}
.category_overview .search_boy_with_input .search_boy_input #category_last_but {
	min-width: 10vw;
}

/*************/

@media screen and (max-width: 992px) {
	.category_overview {
		overflow: hidden;
	}

	.category_overview .staticContent {
		top: 0px;
	}

	.category_overview .scrollContent {
		margin-top: 0px;
	}

	.category_overview .pagePath {
		display: none;
	}

	.category_overview .categoriesBackground {
		background-color: #fff;
		width: auto;
	}

	.category_overview .categoriesBackground ::-webkit-scrollbar {
		display: none;
	}

	.category_overview .categoriesBackground .categories {
		min-width: 100%;
		max-width: 200%;
		padding-left: 5.5%;
		overflow-x: scroll;
	}

	.category_overview .categoriesBackground .categories .categoryButton {
		margin-right: 10px;
		font-weight: bold;
		background-color: #fff;
		overflow: visible;
		font-size: 12px;
		height: 35px;
		min-width: 120px;
		max-width: 240px;
		width: 120px;
	}

	.category_overview .categoriesBackground .categories .selectedButton {
		min-width: 120px;
		max-width: 240px;
		width: 120px;
	}

	.category_overview .subCategoriesBackground {
		width: auto;
	}

	.category_overview .subCategoriesBackground ::-webkit-scrollbar {
		display: none;
	}

	.category_overview .subCategoriesBackground .subCategories {
		display: flex;
		flex-direction: row;
		min-width: 100%;
		padding-left: 5.5%;
		overflow-x: scroll;
	}

	.category_overview .subCategoriesBackground .subCategories hr {
		border-bottom: 5px solid #1bacf4;
		width: 83%;
		margin: 0 auto;
		margin-top: 20px;
		margin-left: 1%;
		opacity: 0;
	}

	.category_overview .subCategoriesBackground .subCategories .subCard {
		margin-top: 20px;
		margin-right: 20px;
		margin-bottom: 0px;
		margin-left: 0px;
		box-shadow: 0 0 10px -1px rgba(0, 0, 0, 0.2);
		padding: 0px 10px 0px 10px;
		border-radius: 10px;
		height: auto;
	}

	.category_overview .subCategoriesBackground .subCategories .subCard p {
		font-weight: 500;
		color: #000;
		padding-bottom: 5px;
		font-size: 9px;
	}

	.category_overview .product_overview {
		max-width: 100%;
	}
	.category_overview .product_overview button {
		font-size: 6px;
		padding: 5px 10px;
	}
	.category_overview .product_overview .category .catcard {
		margin: 20px 0px;
		box-shadow: 0px 2px 7px 2px rgba(0, 0, 0, 0.2);
	}
	.category_overview .product_overview .category .catcard .ccard {
		min-width: 10vw;
		min-height: 170px;
		padding: 8px;
	}
	.category_overview .product_overview .category .catcard .ccard img {
		order: 1;
		max-width: 100%;
		height: auto;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
	}
	.category_overview .product_overview .category .catcard .ccard .cathead {
		order: 2;
		font-size: 6px;
		padding: 3px 4px;
		/* min-width: 100px; */
	}
	.category_overview .product_overview .category .catcard .ccard .description {
		font-size: 9px;
	}

	.category_overview .search_boy_with_input .search_boy_input {
		display: flex;
		flex-direction: column;
		padding: 0px;
	}
	.category_overview .search_boy_with_input .search_boy img {
		max-width: 100%;
	}

	.category_overview .search_boy_with_input .search_boy_input input {
		margin: 10px 0px;
		min-width: 90vw;
	}
	.category_overview .search_boy_with_input .search_boy_input button {
		background-color: #0b90d3;
		width: 135px;
		margin: auto;
	}
}
